body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
  color: white;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.progress-bar {
  position: absolute;
  top: 10px;
  width: 90%;
  display: flex;
  justify-content: space-between;
}

.progress-segment {
  width: 15%;
  height: 4px;
  background-color: rgba(255, 255, 255, 0.3);
  margin: 0 2px;
  transition: width 0.3s;
}

.progress-segment.active {
  background-color: white;
}

.slide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  animation: fade 1s;
}

h1 {
  font-size: 3em;
  text-align: center;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
